CACA,gBACE,wBACF,CACA,uBACE,eACF,CAEA,gDAEE,sBACF,CACA,8EAEE,cACF,CAEA,oDAEE,sBAA2B,CAC3B,cACF,CACA,kFAEE,wBACF,CAGA,YACE,0DAAgE,CAChE,OAAU,CACV,QAAW,CACX,eAAgB,CAChB,cAAe,CACf,iBAAkB,CAClB,WAAY,CAGZ,qBAAsB,CACtB,uCACF,CAEA,iCACE,eACF,CAEA,mCACE,eACF,CAEA,wBACE,YAAa,CACb,aACF,CAEA,wBACE,WAAY,CACZ,cACF,CAEA,uBACE,UACF,CAEA,sBACE,SACF,CAEA,wBACE,QACF,CACA,mCACE,WACF,CAEA,qBACE,KACF,CACA,gCACE,QACF,CAEA,4BACE,mCAAuC,CAGvC,qBAAsB,CACtB,uCACF,CAGA,sBACE,YACF,CAEA,cACE,iBAAkB,CAClB,YAAa,CACb,uCAA+C,CAC/C,cAAe,CACf,kCACF","file":"index.css","sourcesContent":["/*\r\n * Explanation why the layout looks so complicated:\r\n * The UI container needs a position (absolute or relative) to prevent z-index issues (DomMarker on top of UI)\r\n * Therefore it has these additional styles:\r\n *    position: absolute;\r\n *    width: 100%;\r\n *    height: 100%;\r\n * To prevent that the UI container captures all events the container is displaced by\r\n *   left: 100%;\r\n * To neutralize the displacement for the UI elements within the UI container the following adjustments are needed:\r\n *  - InfoBubble (.H_ib):            left: -100%;\r\n *  - left anchor (.H_l_left):       margin-left: -100%;\r\n *  - center anchor (.H_l_center):   left: -50%;            (was left: 50%)\r\n *  - right anchor (.H_l_right):     right: 100%;           (was right: 0)\r\n *                                        margin-left: -100%;\r\n */\r\n\r\n.H_ui {\r\n  font-size: 10px;\r\n  font-family: \"Lucida Grande\", Arial, Helvetica, sans-serif;\r\n  -moz-user-select: none;\r\n  -khtml-user-select: none;\r\n  -webkit-user-select: none;\r\n  -o-user-select: none;\r\n  -ms-user-select: none;\r\n  /* position ui on top of imprint to make both clickable */\r\n  z-index: 2;\r\n  position: absolute;\r\n  width: 100%;\r\n  height: 100%;\r\n  left: 100%;\r\n}\r\n.H_ui * {\r\n  /* normalize in case some other normalization CSS likes things differently */\r\n  box-sizing: content-box;\r\n  -moz-box-sizing: content-box;\r\n}\r\n.H_noevs {\r\n  pointer-events: none;\r\n}\r\n\r\n/*\r\n * Layout\r\n */\r\n.H_l_left {\r\n  position: absolute;\r\n  left: 16px;\r\n  margin-left: -100%;\r\n}\r\n.H_l_center {\r\n  position: absolute;\r\n  left: -50%;\r\n}\r\n.H_l_right {\r\n  position: absolute;\r\n  right: calc(100% + 16px);\r\n  margin-left: -100%;\r\n}\r\n.H_l_top {\r\n  top: 16px;\r\n}\r\n.H_l_middle {\r\n  top: 50%;\r\n}\r\n.H_l_bottom {\r\n  bottom: 16px;\r\n}\r\n\r\n/* Fix MAPSJS-579 for modern browsers */\r\n[class^=H_l_] {\r\n  pointer-events: none;\r\n}\r\n.H_ctl {\r\n  /* hack for IE9-10, auto doesn't work for them */\r\n  pointer-events: visiblePainted;\r\n  pointer-events: auto;\r\n}\r\n\r\n.H_l_horizontal .H_ctl {\r\n  float: left;\r\n}\r\n\r\n.H_l_anchor {\r\n  clear: both;\r\n  float: right;\r\n}\r\n\r\n.H_l_vertical .H_ctl {\r\n  clear: both;\r\n}\r\n\r\n.H_l_right .H_l_vertical .H_ctl {\r\n  float: right;\r\n}\r\n\r\n.H_l_right.H_l_middle.H_l_vertical .H_ctl{\r\n  float: right;\r\n}\r\n\r\n/**\r\n *  Element styles\r\n */\r\n\r\n.H_ctl {\r\n  margin: .8em;\r\n  position: relative;\r\n  -ms-touch-action: none;\r\n}\r\n\r\n.H_btn {\r\n  cursor: pointer;\r\n}\r\n\r\n.H_grp .H_btn,\r\n.H_rdo_buttons .H_btn {\r\n  box-shadow: none;\r\n}\r\n.H_grp .H_btn.H_active,\r\n.H_rdo_buttons .H_btn.H_active {\r\n  background: none;\r\n}\r\n\r\n.H_btn {\r\n  box-shadow: 0em 0 0.4em 0 rgba(15, 22, 33, 0.6);\r\n  border-radius: 0.5em;\r\n  width: 4em;\r\n  height: 4em;\r\n  background: #fff;\r\n}\r\n\r\n.H_disabled,\r\n.H_disabled:hover {\r\n  cursor: default;\r\n}\r\n\r\n.H_rdo_title {\r\n  font-size: 14px;\r\n  height: 40px;\r\n  line-height: 40px;\r\n  background-color: #575B63;\r\n  color: #fff;\r\n  padding-left: 16px;\r\n  padding-right: 16px;\r\n  border-radius: 5px 5px 0 0;\r\n  margin-bottom: 8px;\r\n  cursor: default;\r\n}\r\n.H_rdo ul {\r\n  list-style: none;\r\n  margin: 0 auto;\r\n  padding: 0;\r\n}\r\n\r\n/**\r\n *   Base Elements\r\n */\r\n.H_ctl.H_grp {\r\n  background: #fff;\r\n  box-shadow: 0em 0 0.4em 0 rgba(15, 22, 33, 0.6);\r\n  border-radius: 0.5em;\r\n}\r\n/* Button divider */\r\n.H_zoom .H_el {\r\n  position: relative;\r\n}\r\n.H_l_vertical .H_zoom .H_el:last-child::after,\r\n.H_l_horizontal .H_zoom .H_el:last-child::after {\r\n  content: none;\r\n}\r\n\r\n.H_l_vertical .H_zoom .H_el {\r\n  margin-bottom: 0.1em;\r\n}\r\n.H_l_vertical .H_zoom .H_el:last-child {\r\n  margin-bottom: 0;\r\n}\r\n.H_l_vertical .H_zoom .H_el::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  width: 2.6em;\r\n  height: 0.1em;\r\n  bottom: -0.1em;\r\n  left: 0.7em;\r\n  background: rgba(15, 22, 33, 0.1);\r\n}\r\n\r\n.H_l_horizontal .H_zoom .H_el {\r\n  margin-right: 0.1em;\r\n}\r\n.H_l_horizontal .H_zoom .H_el:last-child {\r\n  margin-right: 0;\r\n}\r\n.H_l_horizontal .H_zoom .H_el::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  width: 0.1em;\r\n  height: 2.6em;\r\n  top: 0.7em;\r\n  right: -0.1em;\r\n  background: rgba(15, 22, 33, 0.1);\r\n}\r\n/* End: Button divider */\r\n.H_l_horizontal .H_grp .H_btn,\r\n.H_l_vertical .H_ctl {\r\n  float: left;\r\n}\r\n\r\n\r\n/** Menu panel */\r\n.H_overlay {\r\n  font-size: 14px;\r\n  color: rgba(15, 22, 33, 0.6);\r\n  box-shadow: 0px 0 4px 0 rgba(15, 22, 33, 0.6);\r\n  border-radius: 5px;\r\n  position: absolute;\r\n  min-width: 200px;\r\n  background: #fff;\r\n  display: none;\r\n  z-index: 100;\r\n  padding-bottom: 4px;\r\n}\r\n\r\n.H_overlay .H_separator {\r\n  content: \"\";\r\n  position: relative;\r\n  display: block;\r\n  margin: 8px 16px 8px 16px;\r\n  height: 1px;\r\n  background: rgba(15, 22, 33, 0.1);\r\n}\r\n\r\n.H_overlay .H_btn,\r\n.H_overlay .H_rdo li {\r\n  width: 184px;\r\n  height: 24px;\r\n  line-height: 24px;\r\n  padding: 8px 16px;\r\n}\r\n.H_overlay .H_btn{\r\n  border-radius: 0px;\r\n}\r\n\r\n.H_overlay .H_btn:hover,\r\n.H_overlay .H_rdo li:hover {\r\n  color: rgba(15, 22, 33, 0.8);\r\n}\r\n\r\n.H_overlay .H_btn.H_disabled,\r\n.H_overlay .H_rdo.H_disabled li,\r\n.H_overlay