E,eAAA","file":"index.vue","sourcesContent":["/* Variables */\r\n\r\n// Base color\r\n$blue:#324157;\r\n$light-blue:#3A71A8;\r\n$red:#C03639;\r\n$pink: #E65D6E;\r\n$green: #30B08F;\r\n$tiffany: #4AB7BD;\r\n$yellow:#FEC171;\r\n$panGreen: #30B08F;\r\n\r\n// Sidebar\r\n$sideBarWidth: 210px;\r\n$subMenuBg:#1f2d3d;\r\n$subMenuHover:#001528;\r\n$subMenuActiveText:#f4f4f5;\r\n$menuBg:#304156;\r\n$menuText:#bfcbd9;\r\n$menuActiveText:#409EFF; // Also see settings.sidebarTextTheme\r\n\r\n// Login page\r\n$lightGray: #eee;\r\n$darkGray:#889aa4;\r\n$loginBg: #2d3a4b;\r\n$loginCursorColor: #fff;\r\n\r\n// The :export directive is the magic sauce for webpack\r\n// https://mattferderer.com/use-sass-variables-in-typescript-and-javascript\r\n:export {\r\n  menuBg: $menuBg;\r\n  menuText: $menuText;\r\n  menuActiveText: $menuActiveText;\r\n}\r\n/* Mixins */\r\n@mixin clearfix {\r\n  &:after {\r\n    content: \"\";\r\n    display: table;\r\n    clear: both;\r\n  }\r\n}\r\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\r\n.app-wrapper {\r\n  @include clearfix;\r\n  position: relative;\r\n  height: 100%;\r\n  width: 100%;\r\n}\r\n\r\n.drawer-bg {\r\n  background: #000;\r\n  opacity: 0.3;\r\n  width: 100%;\r\n  top: 0;\r\n  height: 100%;\r\n  position: absolute;\r\n  z-index: 999;\r\n}\r\n\r\n.main-container {\r\n  min-height: 100%;\r\n  transition: margin-left 0.28s;\r\n  margin-left: $sideBarWidth;\r\n  position: relative;\r\n}\r\n\r\n.sidebar-container {\r\n  transition: width 0.28s;\r\n  width: $sideBarWidth !important;\r\n  height: 100%;\r\n  position: fixed;\r\n  font-size: 0px;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  z-index: 1001;\r\n  overflow: hidden;\r\n}\r\n\r\n.hideSidebar {\r\n  .main-container {\r\n    margin-left: 54px;\r\n  }\r\n\r\n  .sidebar-container {\r\n    width: 50px !important;\r\n  }\r\n}\r\n\r\n/* for mobile response 适配移动端 */\r\n.mobile {\r\n  .main-container {\r\n    margin-left: 0px;\r\n  }\r\n\r\n  .sidebar-container {\r\n    transition: transform 0.28s;\r\n    width: $sideBarWidth !important;\r\n  }\r\n\r\n  &.openSidebar {\r\n    position: fixed;\r\n    top: 0;\r\n  }\r\n\r\n  &.hideSidebar {\r\n    .sidebar-container {\r\n      pointer-events: none;\r\n      transition-duration: 0.3s;\r\n      transform: translate3d(-$sideBarWidth, 0, 0);\r\n    }\r\n  }\r\n}\r\n\r\n.withoutAnimation {\r\n  .main-container,\r\n  .sidebar-container {\r\n    transition: none;\r\n  }\r\n}\r\n"]}]);
