-active {\r\n  transition: opacity 0.28s;\r\n}\r\n\r\n.fade-enter,\r\n.fade-leave-active {\r\n  opacity: 0;\r\n}\r\n\r\n// fade-transform\r\n.fade-transform-leave-active,\r\n.fade-transform-enter-active {\r\n  transition: all .5s;\r\n}\r\n\r\n.fade-transform-enter {\r\n  opacity: 0;\r\n  transform: translateX(-30px);\r\n}\r\n\r\n.fade-transform-leave-to {\r\n  opacity: 0;\r\n  transform: translateX(30px);\r\n}\r\n\r\n// breadcrumb\r\n.breadcrumb-enter-active,\r\n.breadcrumb-leave-active {\r\n  transition: all .5s;\r\n}\r\n\r\n.breadcrumb-enter,\r\n.breadcrumb-leave-active {\r\n  opacity: 0;\r\n  transform: translateX(20px);\r\n}\r\n\r\n.breadcrumb-move {\r\n  transition: all .5s;\r\n}\r\n\r\n.breadcrumb-leave-active {\r\n  position: absolute;\r\n}\r\n","/* Recommended css code for vue-svgicon */\r\n.svg-icon {\r\n    display: inline-block;\r\n    width: 16px;\r\n    height: 16px;\r\n    color: inherit;\r\n    fill: none;\r\n    stroke: currentColor;\r\n    vertical-align: -0.15em;\r\n}\r\n\r\n.svg-fill {\r\n    fill: currentColor;\r\n    stroke: none;\r\n}\r\n\r\n.svg-up {\r\n    transform: rotate(0deg);\r\n}\r\n\r\n.svg-right {\r\n    transform: rotate(90deg);\r\n}\r\n\r\n.svg-down {\r\n    transform: rotate(180deg);\r\n}\r\n\r\n.svg-left {\r\n    transform: rotate(-90deg);\r\n}\r\n","  /* 封面背景及主题背景，可以设置图片 */\r\n  $album_background: #0091e4;\r\n  $theme_background: #f9fcff;\r\n  $main_background: #e5f4ff;\r\n\r\n  /* 导航及tab相关颜色 */\r\n  $color_nav_background: #fff;\r\n  $color_nav_font: #333;\r\n  $color_nav_active_background: #e5f4ff;\r\n  $color_nav_active_font: #0091e4;\r\n\r\n  /* 输入框颜色 */\r\n  $color_chat_editor_background: #e5f4ff;\r\n  $color_chat_editor_button: #0091e4;\r\n\r\n  /* 基础按钮颜色 */\r\n  $color_base_font: #333;\r\n  $color_error: #ed0e0e;\r\n  $color_tip_background: #e0e0e0;\r\n  $color_tip_font: #ccc;\r\n  $color_button_font: #fff;\r\n  $color_button_primary: #0091e4;\r\n  $color_button_warn: #f04d64;\r\n\r\n  $color_input_dark: #333;\r\n  $color_input_light: #999;\r\n\r\n  /* 设置颜色变量 */\r\n  $color_white: #fff;\r\n\r\n  $height_editor: 70px;\r\n  $height_editor_top:20px;\r\n\r\n  /* 消息变量 */\r\n  $msg_bg_you: #5cacde;\r\n  $msg_tx_you: #fff;\r\n  $msg_bg_me: #e5f4ff;\r\n  $msg_tx_me: #666;\r\n\r\n\r\n  .list,\r\n  .chat,\r\n  .info {\r\n    float: left;\r\n    width: 300px;\r\n    padding: 10px;\r\n  }\r\n\r\n  .chat {\r\n    width: calc(100% - 600px);\r\n    min-width: 300px;\r\n  }\r\n\r\n  .info {\r\n    width: 300px;\r\n  }\r\n\r\n  .p-session {\r\n    border: 1px $main_background solid;\r\n  }\r\n\r\n  .m-article {\r\n    border: 1px $main_background solid;\r\n  }\r\n\r\n  .m-main {\r\n    min-height: 650px;\r\n  }\r\n\r\n  /* 二级页面的tab */\r\n  .m-tab {\r\n    color: #0091e4;\r\n    background-color: $main_background;\r\n    margin: 0;\r\n    padding: 10px;\r\n    width: 100%;\r\n    overflow: hidden;\r\n    z-index: 3;\r\n\r\n    .vux-header-left,\r\n    .vux-header-right {\r\n      top: 20px;\r\n    }\r\n\r\n    .m-tab-top {\r\n      position: relative;\r\n      padding: 0;\r\n      font-size: 14px;\r\n\r\n      &.vux-button-group {\r\n        margin: 4px auto;\r\n      }\r\n    }\r\n\r\n    .m-tab-top {\r\n      font-size: 14px;\r\n    }\r\n\r\n    .m-tab-right {\r\n      position: absolute;\r\n      right: 0;\r\n      top: 0;\r\n      font-size: 14px;\r\n      text-align: right;\r\n      width: 4rem;\r\n    }\r\n  }\r\n\r\n  /* 消息记录 */\r\n  .u-msg {\r\n    position: relative;\r\n    padding: 0.2rem 0;\r\n\r\n    &.session-chat {\r\n      padding: 0.6rem 0;\r\n      clear: both;\r\n    }\r\n\r\n    /* 用户头像 */\r\n    .msg-head {\r\n      position: relative;\r\n      display: inline-block;\r\n      top: 0.1rem;\r\n      margin: 0;\r\n      margin-right: 0.6rem;\r\n      margin-left: 0.6rem;\r\n      padding: 0;\r\n      width: 2rem;\r\n      height: 2rem;\r\n      vertical-align: top;\r\n\r\n      img {\r\n        position: relative;\r\n        display: inline-block;\r\n        margin: 0;\r\n      }\r\n\r\n\r\n    }\r\n    .u-circle{\r\n            i{\r\n              font-size: 22px;\r\n              border: 1px #eee solid;\r\n              border-radius: 50%;\r\n              padding: 5px;\r\n              background: #eee;\r\n            }\r\n          }\r\n    .msg-user {\r\n      font-style: italic;\r\n      color: #999;\r\n\r\n      em {\r\n        font-weight: normal;\r\n        margin-right: 1rem;\r\n      }\r\n    }\r\n\r\n    .msg-text {\r\n      position: relative;\r\n      display: inline-block;\r\n      max-width: 54%;\r\n      min-height: 1.2rem;\r\n      word-break: break-word;\r\n      height: auto;\r\n      line-height: 1.2rem;\r\n      font-size: 0.9rem;\r\n      border: none;\r\n\r\n      img {\r\n        position: relative;\r\n        display: inline-block;\r\n        margin: 0;\r\n        padding: 0;\r\n        max-width: 100%;\r\n        vertical-align: bottom;\r\n\r\n        &.emoji-small {\r\n          width: 1.4rem;\r\n          height: 1.4rem;\r\n          vertical-align: bottom;\r\n        }\r\n\r\n        &.emoji-big {\r\n          width: 3rem;\r\n          height: 3rem;\r\n          vertical-align: middle;\r\n        }\r\n\r\n        &.emoji-big {\r\n          width: 4.8rem;\r\n          height: 4.8rem;\r\n          vertical-align: middle;\r\n        }\r\n      }\r\n\r\n      embed,\r\n      video {\r\n        position: relative;\r\n        display: inline-block;\r\n        max-width: 100%;\r\n        //max-height: 8rem;\r\n        vertical-align: bottom;\r\n        background-color: #000;\r\n        text-align: center;\r\n        color: #fff;\r\n      }\r\n\r\n      &::before,\r\n      &::after {\r\n        content: ' ';\r\n        position: absolute;\r\n        top: 0.8rem;\r\n        border-top: 0.4rem solid transparent;\r\n        border-bottom: 0.4rem solid transparent;\r\n        width: 0;\r\n        height: 0;\r\n      }\r\n\r\n      &.notify {\r\n        max-width: 100%;\r\n      }\r\n\r\n      .button {\r\n        margin: 0.1rem 0;\r\n        padding: 0.1rem 0.2rem;\r\n        border: 1px solid #fff;\r\n        border-radius: 0.2rem;\r\n        background-color: $color_nav_active_background;\r\n        color: #666;\r\n      }\r\n    }\r\n\r\n    .msg-failed {\r\n      position: relative;\r\n      float: right;\r\n      margin-right: 0.2rem;\r\n      vertical-align: top;\r\n      font-size: 0.92rem;\r\n      line-height: 2.0rem;\r\n    }\r\n\r\n    .msg-link {\r\n      display: block;\r\n      position: absolute;\r\n      bottom: -2rem;\r\n      right: 0;\r\n      min-width: 4rem;\r\n      min-height: 1.2rem;\r\n      word-break: normal;\r\n      height: auto;\r\n      line-height: 1.2rem;\r\n      font-size: 1.0rem;\r\n      border: none;\r\n\r\n      a {\r\n        color: $color_nav_active_font;\r\n        text-decoration: underline;\r\n        float: right;\r\n      }\r\n    }\r\n\r\n    &.session-chat .msg-text {\r\n      padding: 0.4rem;\r\n      margin-bottom: 0.8rem;\r\n      border-radius: 0.4rem;\r\n      box-shadow: 2px 3px 5px 0 rgba(0,0,0,.07);\r\n    }\r\n\r\n    &.session-chat.item-you {\r\n\r\n      .msg-head,\r\n      .msg-text {\r\n        float: left;\r\n      }\r\n\r\n      .msg-text {\r\n        background-color: $msg_bg_you;\r\n        color: $msg_tx_you;\r\n\r\n        &::before {\r\n          right: 99%;\r\n          border-right: 0.4rem solid $msg_bg_you;\r\n        }\r\n\r\n        &::after {\r\n          background: none;\r\n        }\r\n      }\r\n    }\r\n\r\n    &.session-chat.item-me {\r\n\r\n      .msg-head,\r\n      .msg-text {\r\n        float: right;\r\n      }\r\n\r\n      .msg-text {\r\n        color: $msg_tx_me;\r\n        background-color: $msg_bg_me;\r\n        text-align: left;\r\n        &::before {\r\n          background: none;\r\n        }\r\n\r\n        &::after {\r\n          left: 99%;\r\n          border-left: 0.4rem solid $msg_bg_me;\r\n        }\r\n      }\r\n    }\r\n\r\n    &.item-time {\r\n      font-size: 0.8rem;\r\n      text-align: center;\r\n      color: $color_tip_font;\r\n    }\r\n\r\n    &.item-tip {\r\n      font-size: 0.8rem;\r\n      text-align: center;\r\n\r\n      .tip {\r\n        margin: 0 auto;\r\n        width: 10rem;\r\n        padding: 0.1rem;\r\n        background-color: $color_tip_background;\r\n        border-radius: 0.4rem;\r\n      }\r\n    }\r\n    .tips-msg {\r\n      > span {\r\n        display: inline-block;\r\n        background: #f5f5f5;\r\n        color: #999;\r\n        padding: 5px 10px;\r\n        border-radius: 10px;\r\n        margin-bottom: 0.8rem;\r\n      }\r\n    }\r\n\r\n    .notification {\r\n      font-size: 0.8rem;\r\n      text-align: center;\r\n      color: $color_tip_font;\r\n    }\r\n\r\n    .notify {\r\n      color: #f50;\r\n      font-size: 0.8rem;\r\n    }\r\n  }\r\n\r\n  .m-chat-editor {\r\n    width: 100%;\r\n    height: $height_editor;\r\n    background-color: $color_chat_editor_background;\r\n    position: relative;\r\n  }\r\n\r\n  /* 聊天按钮组件 */\r\n  .m-chat-editor-main {\r\n    position: relative;\r\n    //display: block;\r\n    display: flex;\r\n    align-items: center;\r\n    box-sizing: border-box;\r\n    padding: 0;\r\n    height: 100%;\r\n    width: 100%;\r\n\r\n    .u-editor-input {\r\n      position: relative;\r\n      display: inline-block;\r\n      box-sizing: border-box;\r\n      margin: 0;\r\n      padding: 0.2rem;\r\n      //padding-right: 7rem;\r\n      //width: 100%;\r\n      height: $height_editor;\r\n      flex: 1;\r\n\r\n      textarea {\r\n        position: relative;\r\n        display: inline-block;\r\n        box-sizing: border-box;\r\n        padding: 0.2rem;\r\n        font-size: 1rem;\r\n        width: 100%;\r\n        height: 100%;\r\n        text-align: left;\r\n        border: 1px solid #ccc;\r\n        border-radius: 0.4rem;\r\n      }\r\n    }\r\n\r\n    .u-editor-icons {\r\n      //position: absolute;\r\n      display: flex;\r\n      align-items: center;\r\n      //right: 0;\r\n      padding-right: 10px;\r\n      //top: 0;\r\n      //width: 7rem;\r\n      height: $height_editor;\r\n\r\n      .u-editor-icon {\r\n        //position: relative;\r\n        display: inline-block;\r\n        //top: $height_editor_top;\r\n        width: 1.6rem;\r\n        height: 1.6rem;\r\n        margin: 0 10px;\r\n        box-sizing: border-box;\r\n        margin-left: 0.2rem;\r\n        vertical-align: middle;\r\n\r\n        input[type=\"file\"] {\r\n          position: absolute;\r\n          display: inline-block;\r\n          left: 0;\r\n          top: 0;\r\n          width: inherit;\r\n          height: inherit;\r\n          opacity: 0;\r\n          font-size: 1rem;\r\n        }\r\n      }\r\n    }\r\n\r\n    .u-editor-send {\r\n      position: relative;\r\n      display: inline-block;\r\n      top: $height_editor_top;\r\n      margin-left: 0.2rem;\r\n      width: 2.6rem;\r\n      height: 1.6rem;\r\n      line-height: 1.6rem;\r\n      padding: 0.1rem 0.2rem;\r\n      border-radius: 0.2rem;\r\n      font-size: 0.9rem;\r\n      color: $color_button_font;\r\n      background-color: $color_chat_editor_button;\r\n      text-align: center;\r\n      cursor: pointer;\r\n    }\r\n  }\r\n\r\n  .u-error {\r\n    color: $color_error;\r\n  }\r\n\r\n  .u-icon {\r\n    display: inline-block;\r\n    width: inherit;\r\n    height: inherit;\r\n\r\n    &.icon-file {\r\n      background-position: -7.6rem -7rem;\r\n    }\r\n  }\r\n\r\n  .u-icon {\r\n    background-size: 20rem;\r\n    background-image: url(../im/images/im/icons.png);\r\n    position: relative;\r\n  }\r\n\r\n  .u-icon-img {\r\n    position: relative;\r\n    width: inherit;\r\n    height: inherit;\r\n\r\n    img {\r\n      position: relative;\r\n      margin: 0;\r\n      padding: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n    }\r\n  }\r\n\r\n  .u-circle {\r\n    border-radius: 50%;\r\n  }\r\n\r\n  /* 列表 */\r\n  .u-list {\r\n    width: 100%;\r\n    padding-bottom: 50px;\r\n    .u-circle{\r\n      width: 80px;\r\n      i{\r\n        font-size: 15p